.App {
  text-align: center;
  min-height: 100vh;
  background: rgba(0,0,0,.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 40px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

h2{
  font-weight: bold;
  font-size: 18px;
  margin: 20px 0 40px;
  color: #888888;
}

ul{
  list-style-type: none;
  padding-left: 0;
  margin-left: 0;
}

.frame{
  width: 500px;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 20px;
  padding: 40px;
}

.create{
  border-radius: 20px;
}

.add{
  border-radius: 20px;
  padding: 10px 25px;
}

.new{
  color: #999999;
  font-weight: bold;
  font-size: 14px;
  margin: 5px 0 10px;
}

.create-block{
  border: 1px solid #ddd;
  border-radius: 20px;
  padding: 2px;
}

.item__text {
  text-align: left;
}

.item{
  margin-top: 15px;
  padding: 10px 20px;
  border: 1px solid #ddd;
  border-radius: 20px;
}

.item:hover{
  border: 1px solid #4d4;
}

.item__title{
  color: #444;
  text-transform: capitalize;
  font-weight: bold;
  font-size: 16px;
}

.item__count{
  color: #888;
  font-size: 15px;
}

.noMore{
  color: #ff0000;
  font-weight: bold;
  font-size: 13px;
}


